		  },
		  getAllSelect:function(){
			  //产品类型
			  let url= this.Api.getMainProduct
			  let data={}
			  this.myRequest(data,url,'get').then(res => {
			    console.log(res);
			  		_this.productType = res.data.data.list
			   }).catch(err => {
			    wx.showToast({
			      title: err.data.errMsg,
